@import '../../styles/palette.scss';

.team-cards {
    // display: flex;
    //overflow-x: scroll;

    max-height: 64vh;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(5, 1fr);

    .team-card {
        min-width: fit-content;
        display: flex;
        img {
            width: 3rem;
            padding: 0 .25rem;
        }

        a.button {
            padding-left: 0.1rem;
            padding-right: 0.1rem;
            &:hover {
                background-color: rgba($green, 0.7);
            }
        }
    }
}

.teams-table {
    .afo-table-cell {
        padding: .5rem;
        cursor: pointer;

        img {
            width: 3rem;
            padding: .1rem 1rem;
        }
    }
}