@import '../styles/palette.scss';

$stripe:#cfd8dc; 
$bg: $primary;

.app-start {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 15vh;
    width: 100%;
    background: $secondary;
    margin-bottom: 1rem;

    .heading {
        margin-top: 2rem;
        min-height: 1vh;
        display: flex;
        font-size: calc(1.8rem + 2vmin);

        .text {
            justify-content: center;
            width: 100%;
            text-align: center;
            -webkit-text-fill-color: #fae4c7; /* Will override color (regardless of order) */
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: #fff;
        }

        .text-logo {
            height: 10vmin;
            pointer-events: none;
            background-color: #fff;
            border-radius: 1.5rem;
            padding: .5rem -0;
        }

        .text-logo-left {
            display: flex;
            margin-left: 2.5rem;
        }
        
        .text-logo-right {
            display: flex;
            margin-left: auto;
            margin-right: 2.5rem;
        }
    }

    .app-start-action {
        min-height: 32vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        span {
            font-size: 1.25rem;
            margin: 0 0 1.5rem;
            color: #f3eade;
        }

        a {
            margin: 1rem 0 2.5rem;
        }
    }
}

@media (max-height: 620px) {
    .app-start {
        top: 2rem;
    }
}

